import React from 'react'
import { NavLink} from 'react-router-dom'

export default function Header() {
  return (
    <div>
      <ul>
        <li><NavLink to='/' exact>Home</NavLink>
        </li>
        <li><NavLink to='/history'>History</NavLink>
        </li>
        <li><NavLink to='/setting'>Setting</NavLink>
        </li>
      </ul>
    </div>
  )
}
